/** @jsx jsx */
import { jsx } from 'theme-ui'
import { Fragment, useState, useEffect } from 'react'
// import { Trans } from "@lingui/macro";
// import { Heart as HeartIcon } from "@ant-design/icons";
// import { Lock as LockIcon } from "@ant-design/icons";
// import { ClockCircle as ClockIcon } from "@ant-design/icons";
import {
  CircularProgressbarWithChildren,
  buildStyles,
} from 'react-circular-progressbar'
import 'react-circular-progressbar/dist/styles.css'
import { CKIcon, NitroIcon, KwicrIcon } from '../Icons'
import { motion } from 'framer-motion'
import theme from '../../styles/theme'

const slides = [
  {
    client: 'Credit Karma',
    icon: <CKIcon />,
    text: `â€œContractors on paper, employees in real life. This is what makes
            Bitmatica special and unique compared to any other set of
            contractors I've worked with. It's not about the money. It's about
            top notch ownership driven by a deep passion to build high quality
            software.â€`,
    color: '#3DDB93',
  },
  {
    client: 'AVG',
    icon: <NitroIcon />,
    text: `"We had a great experience working with Bitmatica. The team helped to setup infrastructure and architecture that allowed the team to develop a great product."`,
    color: '#EE5B21',
  },
  {
    client: 'Kwicr',
    icon: <KwicrIcon />,
    text: `"Bitmatica quickly understood the situation we were in and educated and guided us through the product and technical designs that needed to be made."`,
    color: 'white',
  },
  {
    client: 'Credit Karma',
    icon: <CKIcon />,
    text: `"We had a great experience working with Bitmatica. The team helped to setup infrastructure and architecture that allowed the team to develop a great product."`,
    color: '#3DDB93',
  },
]
const INTERVAL_TIME = 5000
const INITIAL_INDEX = 0

const QuoteCarousel = ({ inView }) => {
  const [index, setIndex] = useState(INITIAL_INDEX)
  useEffect(() => {
    const interval = setInterval(() => {
      if (index === slides.length - 1) {
        setIndex(INITIAL_INDEX)
      } else {
        setIndex(index + 1)
      }
    }, INTERVAL_TIME)
    return () => clearInterval(interval)
  }, [index])

  const variants = {
    visible: {
      transition: {
        delay: 1,
        staggerChildren: 0.1,
      },
    },
    hidden: {
      transition: {
        staggerChildren: 0.05,
      },
    },
  }
  // console.log({ inView })
  return (
    <div
      sx={{
        display: 'flex',
        height: 'calc(100% - 2rem)',
        alignItems: 'center',
      }}
    >
      <motion.div
        initial={'hidden'}
        animate={inView && 'visible'}
        variants={variants}
        transition={{ delay: 1 }}
        sx={{ display: 'flex', flexDirection: 'column' }}
      >
        {slides.map((slide, i) => (
          <motion.div
            variants={{
              visible: {
                y: 0,
                opacity: 1,
              },
              hidden: {
                y: 50,
                opacity: 0,
              },
            }}
            whileHover={index !== i && { scale: 1.1 }}
            whileTap={index !== i && { scale: 0.9 }}
            onClick={() => setIndex(i)}
            sx={{
              width: '4rem',
              mr: '2rem',
              cursor: 'pointer',
              ':not(:last-child)': { mb: '1rem' },
            }}
          >
            <CircularProgressbarWithChildren
              value={i === index ? 100 : 0}
              strokeWidth={4}
              styles={buildStyles({
                pathColor: theme.colors.primary,
                trailColor: 'transparent',
                pathTransition:
                  i === index
                    ? `stroke-dashoffset ${INTERVAL_TIME}ms ease-out 0s`
                    : 'none',
              })}
            >
              <div
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  justifyContent: 'center',
                  borderRadius: '50%',
                  fontSize: '1.5rem',
                  backgroundColor: slide.color,
                  color: i === index ? 'white' : 'primary',
                  transition: 'all .3s',
                  width: '3rem',
                  height: '3rem',
                  padding: '.7rem',
                }}
              >
                {slide.icon}
              </div>
            </CircularProgressbarWithChildren>
          </motion.div>
        ))}
      </motion.div>
      <div>
        {slides.map((slide, i) => (
          <Fragment>
            <div sx={{ mb: '2rem' }}>{index === i && slide.text}</div>
            <div>{index === i && slide.client}</div>
          </Fragment>
        ))}
      </div>

      {/* <div>{slides.map((slide, i) => index === i && slide.text)}</div> */}
    </div>
  )
}

export default QuoteCarousel
